<template>
    <div v-theme:column="'wide'" id="show-blogs">
        <h1>博客总览</h1>
        <input type="text" placeholder="搜索" v-model="search">
        <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
            <router-link :to="/blog/+blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link>
            <article>{{blog.content | snippet}}</article>            
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    this.axios
      .get("/post-lee.json")
      .then(res => res.data)
      .then(data => {
        let blogs = [];
        for (let key in data) {
          data[key].id = key;
          blogs.push(data[key]);
        }
        this.blogs = blogs;
      });
  },
  computed: {
    filteredBlogs() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  filters: {
    toUppercase(value) {
      return value.toUpperCase();
    },
    snippet(value) {
      return value.slice(0, 100) + "...";
    }
  },
  directives: {
    rainbow(el, binding, vnode) {
      el.style.color =
        "#" +
        Math.random()
          .toString(16)
          .slice(2, 8);
    }
  }
};
</script>

<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted solid;
}

#show-blogs a {
  color: #444;
  text-decoration: none;
}

input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



