<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <p>分类：</p>
        <ul>
            <li v-for="category in blog.categories" :key="category">{{category}}</li>
        </ul>
        <!-- <button @click="editBlog">编辑</button> -->
        <router-link :to="'/edit/'+id" tag="button">编辑</router-link>
        <button @click="deleteBlog">删除</button>
    </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  methods: {
    deleteBlog() {
      this.axios.delete("/post-lee/" + this.id + ".json").then(res => {
        this.$router.push({ path: "/" });
      });
    }
  },
  created() {
    this.axios
      .get(
        "https://wd6412440268wosrml.wilddogio.com/post-lee/" + this.id + ".json"
      )
      .then(res => {
        this.blog = res.data;
      });
  }
};
</script>

<style>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted solid;
}
</style>



