<template>
    <div id="add-blog">
        <h2>添加博客</h2>
        <form v-if="!submitted">
            <label>博客标题</label>
            <input type="text" v-model="blog.title" required>
            <label>博客内容</label>
            <textarea v-model="blog.content" rows="5"></textarea>
            <div id="checkboxes">
                <label>Vue.js</label>
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                <label>Node.js</label>
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                <label>React.js</label>
                <input type="checkbox" value="React.js" v-model="blog.categories">
                <label>Angualar4</label>
                <input type="checkbox" value="Angualar4" v-model="blog.categories">
            </div>
            <label>作者</label>
            <select v-model="blog.author">
                <option v-for="author in authors" :key="author">{{author}}</option>
            </select>
            <button @click.prevent="postBlog" >提交</button>
        </form>
        <div v-else><h2>您的博客发布成功</h2></div>
        <div id="preview">
            <h3>博客总览</h3>
            <p>博客标题：{{blog.title}}</p>
            <p>博客内容：{{blog.content}}</p>
            <ul v-for="category in blog.categories" :key="category">
                <li>{{category}}</li>
            </ul>
            <p>作者：{{blog.author}}</p>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },
      submitted: false,
      authors: ["Dema", "Huangzi", "Yasuo"]
    };
  },
  methods: {
    postBlog() {
      this.axios.post("/post-lee.json", this.blog).then(res => {
        this.submitted = true;
      });
    }
  }
};
</script>

<style scoped>
#add-blog * {
  box-sizing: border-box;
}
#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label {
  display: block;
  margin: 20px 0 1px;
}
input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}
#checkboxes label {
  display: inline-block;
  margin-top: 0;
}
#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}
button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3 {
  margin-top: 10px;
}
</style>



